// InputFields.js
import React from 'react';
import TextField from '@mui/material/TextField';

const InputFields = ({ initialValues, onInputChange, isUsingEur, setIsUsingEur }) => {
  const handleInputChange = (fieldName, value) => {
    const numericValue = parseFloat(value); // Convert the input value to a number
    const sanitizedValue = isNaN(numericValue) ? "" : numericValue; // Use empty string if NaN
    onInputChange(fieldName, sanitizedValue);
  };

  const currencySymbol = () => {
    setIsUsingEur(!isUsingEur);
  };

  return (
    <div>
      <TextField
        label={isUsingEur ? 'Initial Amount (EUR)' : 'Initial Amount (RON)'}
        type="number"
        variant="outlined"
        value={initialValues.initialAmount}
        onChange={(e) => handleInputChange('initialAmount', e.target.value)}
        fullWidth
        margin="normal"
        required // Add the required attribute
      />
      <TextField
        label={isUsingEur ? 'Monthly Deposit(EUR)' : 'Monthly Deposit(RON)'}
        type="number"
        variant="outlined"
        value={initialValues.monthlyDeposit}
        onChange={(e) => handleInputChange('monthlyDeposit', e.target.value)}
        fullWidth
        margin="normal"
        required // Add the required attribute
      />
      <TextField
        label="Interest Rate (%)"
        type="number"
        variant="outlined"
        value={initialValues.interestRate}
        onChange={(e) => handleInputChange('interestRate', e.target.value)}
        inputProps={{
          pattern: "[0-9]*\\.?[0-9]*", // Allow numeric values with decimal point
        }}
        fullWidth
        margin="normal"
        required // Add the required attribute
      />
       <TextField
        label={isUsingEur ? 'Target Monthly Interest(EUR)' : 'Target Monthly Interest (RON)'}
        type="number"
        variant="outlined"
        value={initialValues.targetMonthlyInterestEarned}
        onChange={(e) => handleInputChange('targetMonthlyInterestEarned', e.target.value)}
        fullWidth
        margin="normal"
      />
    </div>
  );
};

export default InputFields;
