// App.js
import React, { useState } from 'react';
import InputFields from './components/InputFields/InputFields';
import ResultsTable from './components/ResultsTable/ResultsTable';
import Footer from './components/MyFooter'; 
import { CssBaseline, ThemeProvider, createTheme, Button, Typography } from '@mui/material';
import { IconButton } from '@mui/material';
import { Brightness4, Brightness7 } from '@mui/icons-material';
import './App.css'; // Import styles.css
import './styles.css';

// Create a custom Material-UI theme
const theme = createTheme({
  typography: {
    fontFamily: 'Roboto, sans-serif', // Use your chosen font
  },
});
const lightTheme = createTheme();
const darkTheme = createTheme({
  palette: {
    mode: 'dark',
  },
});


// Calculation logic function
const calculateResults = (initialAmount, monthlyDeposit, interestRate, targetMonthlyInterestEarned, years) => {
  const results = [];

  let totalAmount = initialAmount;
  for (let year = 1; year <= years; year++) {

    
    const interestEarned = (totalAmount * interestRate) / 100 ; // Monthly interest
    const monthlyInterestEarned = interestEarned / 12; // Calculate monthly interest earned
    const isTargetAchieved = monthlyInterestEarned >= targetMonthlyInterestEarned; // Calculate target achievement
    totalAmount += monthlyDeposit * 12 + interestEarned;

    results.push({
      year,
      startingAmount: totalAmount - (monthlyDeposit * 12 + interestEarned),
      monthlyDeposit: monthlyDeposit*12,
      interestRate,
      interestEarned,
      monthlyInterestEarned,
      totalAmount,
      isTargetAchieved,
    });
  }

  return results;
};

const App = () => {
  const [initialValues, setInitialValues] = useState({
    //initialAmount: 80000,
    //monthlyDeposit : 8000,
    //interestRate: 6,
    isUsingEur: false, // Flag for input value conversion
  });
  const [themeMode, setThemeMode] = useState('light'); // Store the current theme mode
  // Toggle between light and dark mode
  const toggleThemeMode = () => {
    setThemeMode(themeMode === 'light' ? 'dark' : 'light');
  };

  const [results, setResults] = useState([]);
  const [tableCurrency, setTableCurrency] = useState(false); // State for table currency
  const setIsUsingEur = () => {
    setInitialValues((prevValues) => ({
      ...prevValues,
      isUsingEur: !prevValues.isUsingEur,
    }));
  };

  // Event handler for input changes
  const handleInputChange = (field, value) => {
    setInitialValues((prevValues) => ({
      ...prevValues,
      [field]: parseFloat(value), // Convert to float if needed
    }));
  };

  const handleCalculate = () => {
    // Check if all required fields are filled
    if (
      isNaN(initialValues.initialAmount) ||
      isNaN(initialValues.monthlyDeposit) ||
      isNaN(initialValues.interestRate)
    ) {
      alert("Please fill in all required fields.");
      return;
    }
  
    const calculatedResults = calculateResults(
      initialValues.initialAmount,
      initialValues.monthlyDeposit,
      initialValues.interestRate,
      initialValues.targetMonthlyInterestEarned,
      50 // Number of years you want to calculate
    );
  
    // Find the index of the first result that meets the condition
    const firstTargetAchievedIndex = calculatedResults.findIndex(
      (result) => result.isTargetAchieved
    );
  
    // Set the highlight flag for the first result that meets the condition
    if (firstTargetAchievedIndex !== -1) {
      calculatedResults[firstTargetAchievedIndex].highlight = true;
    }
    setResults(calculatedResults);
  };
  



  return (
    <ThemeProvider theme={themeMode === 'light' ? lightTheme : darkTheme}>
      <CssBaseline />
      <div className="App">
      <div className="app-container">
          <Typography variant="h2" gutterBottom className="title">
            Financial Freedom Calculator
          </Typography>
          <IconButton
            color="inherit"
            onClick={toggleThemeMode}
            className="theme-toggle-button"
          >
            {themeMode === 'light' ? <Brightness4 /> : <Brightness7 />}
          </IconButton>
          <div>
          <div className="toggle-button">
          <IconButton
            variant="contained"
            color="primary"
            onClick={() => setTableCurrency(!tableCurrency)}
          >
            {tableCurrency ? <strong>EURO</strong> : <strong>RON</strong>}
          </IconButton>
          </div>
          </div>

              <InputFields
                initialValues={initialValues}
                onInputChange={handleInputChange}
                isUsingEur={initialValues.isUsingEur}
                setIsUsingEur={setIsUsingEur} // Pass the setIsUsingEur prop
              />

          <Button variant="contained" onClick={handleCalculate} color="primary">
            Calculate
          </Button>
          {results.length > 0 && (
          <>

            <ResultsTable results={results} themeMode={themeMode} tableCurrency={tableCurrency} />
            </>
          )}
        </div>
        <Footer />
      </div>
    </ThemeProvider>
  );
};

export default App;


