// ResultsTable.js
import React from 'react';
import '../../styles.css';

const ResultsTable = ({ results, themeMode, tableCurrency }) => {
  const themeModeClass = themeMode === 'light' ? 'light-mode' : 'dark-mode';
  const currencySymbol = tableCurrency ? 'EUR' : 'RON';


  return (
    <table>
      <thead className={themeModeClass}>
        <tr>
          <th>Year</th>
          <th>Starting Amount</th>
          <th>Yearly Deposit</th>
          <th>Interest Rate (%)</th>
          <th>Yearly Interest Earned</th>
          <th>Monthly Interest Earned</th>
          <th>Total Amount</th>
        </tr>
      </thead>
      <tbody>
        {results.map((result, index) => (
          <tr key={index}
          className={result.highlight ? 'highlighted-row' : ''}
          >
            <td>{result.year}</td>
            <td>{tableCurrency
                ? (result.startingAmount / 5).toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })
                : result.startingAmount.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })}{' '}
              {currencySymbol}</td>
            <td>{tableCurrency
                ? (result.monthlyDeposit / 5).toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })
                : result.monthlyDeposit.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })}{' '}
              {currencySymbol}</td>
            <td>{result.interestRate.toFixed(2)}</td>
            <td>{tableCurrency
                ? (result.interestEarned / 5).toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })
                : result.interestEarned.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })}{' '}
              {currencySymbol}</td>
            <td>{tableCurrency
                ? (result.monthlyInterestEarned / 5).toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })
                : result.monthlyInterestEarned.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })}{' '}
              {currencySymbol}</td>
            <td>{tableCurrency
                ? (result.totalAmount / 5).toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })
                : result.totalAmount.toLocaleString(undefined, {
                    minimumFractionDigits: 2,
                    maximumFractionDigits: 2,
                  })}{' '}
              {currencySymbol}</td>
          </tr>
        ))}
      </tbody>
    </table>
  );
};


export default ResultsTable;